@import url(https://fonts.googleapis.com/css?family=Quantico:700&display=swap);
.Clock{font-family:"Quantico";font-size:calc(32vmin)}.Hurry{color:#FF752A;-webkit-animation:seconds infinite 1s linear;animation:seconds infinite 1s linear}.Hurryx2{color:#FF4E39;-webkit-animation:seconds infinite .80s linear;animation:seconds infinite .80s linear}@-webkit-keyframes seconds{0%{-webkit-transform:scale(1);transform:scale(1)}50%{-webkit-transform:scale(0.9);transform:scale(0.9)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes seconds{0%{-webkit-transform:scale(1);transform:scale(1)}50%{-webkit-transform:scale(0.9);transform:scale(0.9)}100%{-webkit-transform:scale(1);transform:scale(1)}}body{margin:0}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 2s linear;
            animation: App-logo-spin infinite 2s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  0% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
  }
  50% {
      -webkit-transform: scale(.90);
              transform: scale(.90);
  }
  100% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
  }
  /* from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  } */
}

@keyframes App-logo-spin {
  0% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
  }
  50% {
      -webkit-transform: scale(.90);
              transform: scale(.90);
  }
  100% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
  }
  /* from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  } */
}

